* { touch-action: none; }
.assemble{
    background: #666666;
    .assem-child{
        background: white;
        height: 20.2rem;
        margin-bottom: .3rem;
        .assem-top{
        position: relative;
        width: 100%;
        height:13rem;
        margin-bottom: .2rem;
            span{
                font-size: 12px;color:#666;
                cursor: pointer;text-align: center;display: block;
                position: absolute;
                padding:4px 7px;
                top:50%;
                left: 0;
                border: 1px solid #666;
                border-top-right-radius: 1.5625rem;
                border-bottom-right-radius: 1.5625rem;
                background: rgba(251,251,251,.8);
            
            }
            img{
                width: 12.5rem;
                height: 12.5rem;
                display: block;
                margin: 0 auto;
                
                
            }
    }
        .assem-center{
            margin: 0 2%;
            text-align: center;
            font-size: 14px;
            color:#333;
            margin-top: 10px;

    }
        .assem-bottom{
            padding: 1rem;
            display: flex;
            text-align: center;
            justify-content: space-between;
            .leftprice{
                height: 3rem;
                width: 6rem;
                span:nth-child(1){
                    font-size: 14px;
                    color:#fe4070;

                }
                span:nth-child(2){
                    color: #999;
                    font-size: 12px;
                    padding-left: 4px;
                    text-decoration: line-through;
                }
                span:nth-child(3){
                    display: block;
                    font-size: 11px;
                    color: #999;
                }
                // span:nth-child(4){
                //     font-size: 11px;
                //     color: #999;
                //     display: inline-block;
                // }

            }
            .rightprice{
                background: #fe4070;
                color:white;
                line-height: 2.17rem;
                height: 2.17rem;
                width:7.33rem ;
                font-size: 18.75px;
                border-radius: 25px;
                
            }
        }
    }
    
}